import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Tooltip from "@mui/material/Tooltip";

const dispatchedEvents = [];

const SrsMuiTooltip = function (props, ref) {
  return (
    <Tooltip {...props} theme={props.muiTheme}>
      <slot />
    </Tooltip>
  );
};

const styles = `
  /* Custom styles for the MuiTooltip component */
`;

const props = [
  {
    name: "title",
    defaultValue: "",
    type: "string",
  },
  {
    name: "arrow",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableFocusListener",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableHoverListener",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "disableTouchListener",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "enterDelay",
    defaultValue: 0,
    type: "number",
  },
  {
    name: "enterTouchDelay",
    defaultValue: 700,
    type: "number",
  },
  {
    name: "placement",
    defaultValue: "bottom",
    type: "string",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Tooltip if needed
];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiTooltip),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
